:root {
    --color-border: #002e3a;
    --color-border-hover: #0085a7;
    --bg-gradient-hover: #004e61;
    --width-border: 4px;
    --type-border: groove;
}

.Projects_container {
    width: 100%;
    height: max-content;

    display: flex;
    flex-direction: column;

    row-gap: 100px;

    margin: 0 auto;

    padding: 25% 0 10% 0;

    color: white;
}

.Projects_container h2 {
    margin: 0 auto;

    font-size: 3em;
}

.project_list {
    width: 100%;

    display: flex;
    flex-direction: row;
    flex-wrap: wrap;

    row-gap: 200px;
}

.project:nth-child(2n) {
    position: relative;
    left: 100%;
    transform: translateX(-100%);

}

.project {
    width: 668px;

    display: flex;
    flex-direction: column;

    row-gap: 25px;

    backdrop-filter: blur(20px);

    border: 1px solid gray;

    padding: 50px;

    position: relative;
}

.project:hover {
    border-color: white;
}

.project_name {
    margin-left: 40px;

    font-weight: bold;
    font-size: 1.2em;

    width: max-content;

    border-right: var(--width-border) var(--type-border) var(--color-border);
    border-left: var(--width-border) var(--type-border) var(--color-border);

    border-radius: 5px;

    position: relative;

    background-image: linear-gradient(180deg, #002e3a69, black, rgba(0, 46, 58, 0.642));

    transition: .7s ease-in-out;

    box-shadow: none;

    padding: 2px 10px;

    z-index: 2;
}

.project:hover .project_name {
    border-color: var(--color-border-hover);

    transition-delay: .5s;

    background-image: linear-gradient(180deg, var(--bg-gradient-hover), black, var(--bg-gradient-hover));

}

.project_name::after {
    content: '';

    position: absolute;
    top: -50px;

    width: 50%;
    height: 60px;

    margin-left: 14px;

    border-right: var(--width-border) var(--type-border) var(--color-border);
    border-bottom: var(--width-border) var(--type-border) var(--color-border);

    transition: .7s ease-in-out;

    box-shadow: none;

    z-index: 0;
}

.project:hover .project_name::after {
    border-color: var(--color-border-hover);
}

.project .project_image {
    display: block;

    height: 300px;
    width: 100%;

    border: var(--width-border) var(--type-border) var(--color-border);

    transition: .5s ease-in-out;

    background: center center no-repeat;
    background-size: 100% 100%;
    background-color: black;
}

.project:hover .project_image {
    border-color: var(--color-border-hover);
}

.project_image::before {
    content: '';

    border-top: var(--width-border) var(--type-border) var(--color-border);
    border-left: var(--width-border) var(--type-border) var(--color-border);

    transition: .7s ease-in-out;

    width: 42px;
    height: 50px;

    position: absolute;
    top: 58px;
    left: 50px;
}

.project:hover .project_image::before {
    border-color: var(--color-border-hover);
}

.project_image::after {
    content: '';
    border-left: var(--width-border) var(--type-border) var(--color-border);

    transition: .7s ease-in-out;

    width: 0px;
    height: 27px;

    position: absolute;
    top: 403px;
    left: 50%;
    transform: translateX(-50%);
}

.project:hover .project_image::after {
    border-color: var(--color-border-hover);
}

.project_image:hover {
    transition: .6s;
    box-shadow: 10px 10px 20px rgba(0, 0, 0, 0.453);

}

