    background-image: url(../images/project-2.webp);
}

.project .project3 {
    background-image: url(../images/project-3.webp);
}

.project .project4 {
    background-image: url(../images/project-4.webp);
}

.project .project5 {
    background-image: url(../images/project-5.webp);
}

.project .project6 {
    background-image: url(../images/project-6.webp);
}

.project .project7 {
    background-image: url(../images/project-7.webp);
}

.project .project8 {
    background-image: url(../images/project-8.webp);
}


