}

.project:hover .project7 {
    background-image: url(../images/project-7.gif);
}

.project:hover .project8 {
    background-image: url(../images/project-8.gif);
}


.project_description {
    background-image: linear-gradient(180deg, #002e3a69, black, rgba(0, 46, 58, 0.642));

    border: var(--width-border) var(--type-border) var(--color-border);

    transition: .7s ease-in-out;

    padding: 10px;

    text-align: justify;

    color: gray;
    text-shadow: 1px 1px 2px black;

    font-size: 1em;
    font-weight: bold;
}

.project:hover .project_description {
    border-color: var(--color-border-hover);

    transition-delay: .5s;
    color: white;

    background-image: linear-gradient(180deg, var(--bg-gradient-hover), black, var(--bg-gradient-hover));
}

.project_description::before {
    content: '';

    position: absolute;

    top: 160px;
    right: 21px;

    height: 300px;
    width: 25px;

    border-top: var(--width-border) var(--type-border) var(--color-border);
    border-right: var(--width-border) var(--type-border) var(--color-border);
    border-bottom: var(--width-border) var(--type-border) var(--color-border);

    transition: .7s ease-in-out;
}

.project:hover .project_description::before {
    border-color: var(--color-border-hover);
}

.project_description::after {
    content: '';

    position: absolute;

    top: 160px;
    left: 23px;

    height: 300px;
    width: 25px;

    border-top: var(--width-border) var(--type-border) var(--color-border);
    border-left: var(--width-border) var(--type-border) var(--color-border);
    border-bottom: var(--width-border) var(--type-border) var(--color-border);

    transition: .7s ease-in-out;
}

.project:hover .project_description::after {
    border-color: var(--color-border-hover);
}

.project_Container_Skill_links {
    display: flex;
    justify-content: space-between;

    column-gap: 50px;

    width: 100%;
    height: max-content;
}

.project_Container_Skill_links::before {
    content: '';

    position: absolute;
    top: 468px;
    left: 22px;

    height: 160px;
    width: 25px;

    border-bottom: var(--width-border) var(--type-border) var(--color-border);

    border-left: var(--width-border) var(--type-border) var(--color-border);

    transition: .7s ease-in-out;
}

.project:hover .project_Container_Skill_links::before {
    border-color: var(--color-border-hover);
}

.project_Container_Skill_links::after {
    content: '';

    position: absolute;
    top: 468px;
    right: 21px;

    height: 160px;
    width: 25px;

    border-bottom: var(--width-border) var(--type-border) var(--color-border);

    border-right: var(--width-border) var(--type-border) var(--color-border);

    transition: .7s ease-in-out;
}

.project:hover .project_Container_Skill_links::after {
    border-color: var(--color-border-hover);
}

.project_links::after {
    content: '';

    position: absolute;
    bottom: 128px;
    right: 100px;

    height: 26px;
    width: 0px;

    border-right: var(--width-border) var(--type-border) var(--color-border);

    transition: .7s ease-in-out;
}

.project:hover .project_links::after {
    border-color: var(--color-border-hover);
}

.project_links ul {
    display: flex;
    justify-content: space-between;

    border-radius: 20px;

    height: min-content;
    width: 200px;

    padding: 10px;

    border: var(--width-border) var(--type-border) var(--color-border);

    background-image: linear-gradient(180deg, #002e3a69, black, rgba(0, 46, 58, 0.642));

    transition: .7s ease-in-out;

    column-gap: 10px;
}

.project:hover .project_links ul {
    border-color: var(--color-border-hover);

    transition-delay: .5s;

    background-image: linear-gradient(180deg, var(--bg-gradient-hover), black, var(--bg-gradient-hover));

}

.project_links ul::before {
    content: '';

    position: absolute;
    bottom: 26px;
    left: 65px;

    height: 20px;
    width: 425px;

    border-bottom: var(--width-border) var(--type-border) var(--color-border);

    border-right: var(--width-border) var(--type-border) var(--color-border);

    border-left: var(--width-border) var(--type-border) var(--color-border);

    transition: .7s ease-in-out;
}

.project:hover .project_links ul::before {
    border-color: var(--color-border-hover);
}

.project_links ul::after {
    content: '';

    position: absolute;
    bottom: 0px;
    right: 500px;

    height: 27px;
    width: 0px;

    border-right: var(--width-border) var(--type-border) var(--color-border);

    transition: .7s ease-in-out;
}

.project:hover .project_links ul::after {
    border-color: var(--color-border-hover);
}

.project_links svg {
    background-color: rgba(0, 0, 0, 0.53);

    font-size: 3em;

    box-shadow: 2px 3px 5px rgba(0, 0, 0, 0.512);
}

.project_softSkills {
    display: flex;
    justify-content: space-evenly;

    background-image: linear-gradient(180deg, #002e3a69, black, rgba(0, 46, 58, 0.642));

    border-radius: 20px;

    width: 100%;

    padding: 10px;

    border: var(--width-border) var(--type-border) var(--color-border);

    transition: .7s ease-in-out;
}

.project:hover .project_softSkills {
    border-color: var(--color-border-hover);

    background-image: linear-gradient(180deg, var(--bg-gradient-hover), black, var(--bg-gradient-hover));
}

.project_softSkills::before {
    content: '';

    position: absolute;
    bottom: 128px;
    left: 205px;

    height: 10px;
    width: 155px;

    border-top: var(--width-border) var(--type-border) var(--color-border);

    border-right: var(--width-border) var(--type-border) var(--color-border);

    border-left: var(--width-border) var(--type-border) var(--color-border);

    transition: .7s ease-in-out;
}

.project:hover .project_softSkills::before {
    border-color: var(--color-border-hover);
}

.project_softSkills::after {
    content: '';

    position: absolute;
    bottom: 86px;
    left: 250px;

    height: 0px;
    width: 50px;

    border-top: var(--width-border) var(--type-border) var(--color-border);

    transition: .7s ease-in-out;
}

.project:hover .project_softSkills::after {
    border-color: var(--color-border-hover);
}

.project_softSkills svg {
    font-size: 3em;
}

@media (max-width: 768px) {
    .project_image {
        height: 250px;
    }

    .project_image::after {
        top: 403px;
    }

    .project_links ul::before {
        bottom: 26px;
        left: 65px;

        height: 20px;
        width: 255px;
    }

    .project_links ul::after {
        bottom: 0px;
        right: 400px;
    
        height: 26px;
        width: 0px;
    }

    .project_links ul {
        width: 150px;
    }

    .project_softSkills::before {
        left: 160px;

        height: 10px;
        width: 105px;
    }

    .project_softSkills::after {
        left: 200px;
    
        height: 0px;
        width: 52px;
    }

    .project_Container_Skill_links::after,
    .project_Container_Skill_links::before {
        height: 180px;
    }
}


@media (max-width: 568px) {
    .project {
       margin: 0 -24px;
    }

    .project:nth-child(2n) {
        left: 0%;
        transform: translateX(0%);
    
    }

    .project_description {
        max-height: 150px;

        overflow-y: scroll;
    }

    .project_description::-webkit-scrollbar {
        background-color: transparent;
    
        width: 8px;
    }
    .project_description::-webkit-scrollbar-thumb {
        border-radius: 10px;
    
        background-image: linear-gradient(90deg,var(--color-border-hover), black ,var(--color-border-hover));
    }

} 


@media (max-width: 493px) {
    .project_name::after {
        width: 60px;
    }

    .project_image {
        max-height: 200px;
    }

    .project_image::after {
        top: 303px;
    }
    
    .project_description::after, 
    .project_description::before {
        height: 230px;
    }

    .project_Container_Skill_links {
        column-gap: 25px;
    }

    .project_Container_Skill_links::after,
    .project_Container_Skill_links::before {
        height: 130px;
        top: 398px;
    }
    

    .project_links ul {
        width: 120px;
    }

    .project_links ul::after {
        right: 300px;
    }

    .project_links svg,
    .project_softSkills svg {
        font-size: 2em;
    }

    .project_softSkills::before {
        left: 140px;
        bottom: 112px;

        width: 145px;
    }

    .project_softSkills::after {
        left: 170px;
        
        width: 27px;
    }

    .project_links::after {
        bottom: 113px;
    }
} 