:root {
    --color-btn: #dedede;
    --color-btn-hover: #0085a7;
}

.AboutMe_Container {
    height: 100%;
    min-height: 700px;
    width: 100%;

    margin: 0 auto;
    padding: 25% 0;

    display: grid;
    grid-template-columns: 50% 50%;
    grid-template-rows: repeat(3, min-content);

    color: white;

}

.AboutMe_Container>h1 {
    grid-column: 1;

    align-self: flex-end;
}

.AboutMe_Container>p {
    grid-column: 1;

    font-size: 1.05em;
    letter-spacing: 1px;
    line-height: 28px;
    text-transform: capitalize;

    width: 80%;
}

.AboutMe_Container>h2 {
    grid-row: 2;
    grid-column: 2;

    text-align: center;
    font-size: 3em;
    text-shadow: 2px 2px black;
    text-transform: uppercase;

    padding-top: 25px;
}

.AboutMe_Container>h2>span {
    font-size: 1.5em;
}

.button_curri {
    grid-column: 2;
    grid-row: 3;

    margin: 0 auto;

    border-radius: 20px;

}

.button_curri > button {    
    text-transform: uppercase;
    text-decoration: none;

    color: var(--color-btn);

    font: 1.2em;
    font-weight: bold;

    background-color: rgba(0, 0, 0, 0.129);
    border: 2px solid var(--color-btn);

    border-radius: 20px;

    transition: .7s ease-in-out;

    width: 200px;
    height: 40px;

    cursor: pointer;
} 

.button_curri:hover button {
    background-color: #00a7d12a;

    color: var(--color-btn-hover);
    border: 2px solid var(--color-btn-hover);

}

@media (max-width:1124px) {
    .AboutMe_Container {
        display: flex;
        flex-direction: column;

        row-gap: 100px;

        margin-bottom: 100px;

        grid-template-rows: repeat(4, min-content);
    }

    .AboutMe_Container>h2 {
        grid-column: 1;
        grid-row: 1;
    }

    .AboutMe_Container>h2>span {
        font-size: 1em;
    }

    .button_curri {
        grid-column: 1;
        grid-row: 2;
    }

    .AboutMe_Container>h1 {
        grid-row: 3;

        align-self: flex-start;

        margin-left: 10%;
        margin-bottom: -100px;
    }

    .AboutMe_Container>p {
        width: 80%;
        grid-row: 4;

        margin: 0 auto;
    }
}

@media (max-width: 893px) {
    .AboutMe_Container>h1 {
        margin-left: 0%;
    }

    .AboutMe_Container>p {
        width: 100%;
        text-align: justify;
    }
}



@media (max-width: 668px) {
    .AboutMe_Container {
        padding-left: 20px;
        padding-right: 20px;
    }

    .AboutMe_Container>h2 {
        font-size: 2em;
    }

}