.Redes_container {
    width: min-content;

    display: flex;
    align-items: center;

    column-gap: 20px;

    margin-right: 10px;
}

.Redes_container svg {
    font-size: 2em;

    cursor: pointer;
}

.Redes_container svg:hover {
    transition: .5s;
    transform: scale(1.1);
}

@media (max-width:693px) {
    .Redes_container {
        position: absolute;
        top: 10px;
        right: 10px;
    }
}